@import url(https://fonts.googleapis.com/css?family=Montserrat&display=swap);
body {
  margin: 0;
  font-family: "Montserrat", sans-serif;

  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}
* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}
a {
  text-decoration: none;
  display: block;
  color: #121212;
}

.app{position:relative;width:100vw;height:100vh;overflow:scroll;overflow-x:hidden;display:flex;justify-content:center;align-items:flex-start;flex-wrap:wrap;background-color:#fdfdfd;background-image:linear-gradient(90deg, transparent 79px, rgba(171,206,212,0.72549) 79px, rgba(171,206,212,0.72549) 81px, transparent 81px),linear-gradient(#eee 0.1em, transparent 0.1em);background-size:100% 1.2em}

.logo{width:100%;font-size:5vh;background:-webkit-linear-gradient(#8b69fa, #000);-webkit-background-clip:text;-webkit-text-fill-color:transparent}

.button{display:block;color:#121212;text-decoration:none;text-transform:uppercase;font-weight:100;padding:15px 32px;border-radius:6px;border:none;transition:all 0.3s ease 0s;cursor:pointer;letter-spacing:3px;box-shadow:0px 3px 7px -2px rgba(0,0,0,0.52)}.button:hover{transform:scaleX(1.05)}.button:active{position:relative;top:1px}

.home{position:absolute;width:100%;height:100%;margin:auto;opacity:0.9;background-image:url("/React_Portfolio/img/alien.png");background-repeat:no-repeat;background-position:120% 100%;background-size:50%}.home .welcome{position:absolute;display:flex;align-items:center;flex-wrap:wrap;justify-content:flex-start;width:40%;top:40%;left:10%}.home .welcome h1,.home .welcome h2{width:100%;font-weight:300;opacity:0.9}.home .welcome h1{font-size:8vh}.home .welcome h2{font-size:5vh}.home .welcome .projects,.home .welcome .contact{color:white;margin:30px 30px 0 50px;font-weight:700}.home .welcome .projects:hover,.home .welcome .contact:hover{background-color:#0c0c0c}.home .welcome .projects{background-color:#4caf50}.home .welcome .contact{background-color:#008cba}

.nav{width:99%;height:10%;min-height:80px;display:flex;justify-content:space-between;align-items:center;color:#121212;box-shadow:0px 3px 7px -2px rgba(0,0,0,0.52);z-index:1;background-color:#fff;background-image:linear-gradient(90deg, transparent 79px, #abced4 79px, #abced4 81px, transparent 81px),linear-gradient(#eee 0.1em, transparent 0.1em);background-size:100% 1.2em}.nav a .logo{font-weight:900;margin-left:25%}.nav ul{width:50%;display:flex;justify-content:space-evenly;align-items:center}.nav ul a{letter-spacing:2px;transition:transform, box-shadow 0.15s linear 0s;display:flex;padding:10px;justify-content:center;align-items:center}.nav ul a:hover{transform:scaleX(1.05)}@media (max-width: 1000px){.nav ul a{letter-spacing:0}}.nav ul .button{padding:10px;background-color:transparent}.nav ul .login{background-color:#008cba;color:white}.nav ul .logout{border:3px solid #ba006c}@media (max-width: 1000px){.nav ul{padding:0;font-size:0}}.nav ul svg{font-size:1.5rem;opacity:0.7}.nav ul svg:hover{box-shadow:0 4px 2px -2px rgba(0,0,0,0.52)}@media (min-width: 1000px){.nav ul svg{display:none}}

.TV{margin:0 0 10vh 0;height:50vh;position:relative;display:flex;justify-content:center;align-items:center;flex-wrap:wrap;opacity:0.9}.TV__frame{display:flex;justify-content:center;align-items:center;height:44vh;width:65vh;background-image:url("/React_Portfolio/img/tv-cut2.png");background-position:center;background-repeat:no-repeat;background-size:contain;box-shadow:4px 9px 3px -3px rgba(0,0,0,0.253);border-radius:5px}.TV__screen{position:absolute;height:37vh;width:55vh;background-position:center;background-repeat:no-repeat;background-size:cover;transform:translateX(-7%) translateY(-2%);z-index:-1;background-color:#000;transition:background-color 1s;border-radius:10px}.TV__screen-on{-webkit-animation:appear 0.7s linear both;animation:appear 0.7s linear both}.TV__switch{position:absolute;width:4vh;height:4vh;background:#942222;background:radial-gradient(circle, rgba(148,34,34,0.689496) 0%, red 100%);border:solid 5px #000;border-width:1px 5px 5px 1px;border-radius:50%;border-color:transparent #9b0101 #990404 transparent;margin-right:2px;cursor:pointer;transform:translateX(689%) translateY(-360%)}.TV__switch:hover{border-width:0 2px 3px 0;margin-right:4px;background-color:#ff1e00;border-color:transparent #9b0101 #990404 transparent;transform:translateX(692%) translateY(-360%)}.TV__switch-active{border-width:0 2px 3px 0;margin-right:4px;background:#fcb045;background:radial-gradient(circle, #fcb045 2%, #f70000 94%);border-color:transparent #9b0101 #990404 transparent;transform:translateX(692%) translateY(-360%);-webkit-animation:gleam 0.3s linear infinite;animation:gleam 0.3s linear infinite}@-webkit-keyframes gleam{0%{background:radial-gradient(circle, #fcb045 2%, #f70000 94%)}50%{background:radial-gradient(circle, #ec9327 2%, #f70000 94%)}100%{background:radial-gradient(circle, #fcb045 2%, #f70000 94%)}}@keyframes gleam{0%{background:radial-gradient(circle, #fcb045 2%, #f70000 94%)}50%{background:radial-gradient(circle, #ec9327 2%, #f70000 94%)}100%{background:radial-gradient(circle, #fcb045 2%, #f70000 94%)}}@-webkit-keyframes appear{0%{background-color:#121212;background-size:0 0;-webkit-filter:blur(5px);-moz-filter:blur(5px);-o-filter:blur(5px);-ms-filter:blur(5px);filter:blur(5px)}10%{background-color:#121212;background-size:10% 10%}30%{background-color:#121212;background-size:40% 10%}50%{background-color:#121212;background-size:80% 10%}80%{background-size:100% 50%;-webkit-filter:blur(0);-moz-filter:blur(0);-o-filter:blur(0);-ms-filter:blur(0);filter:blur(0)}100%{background-size:100% 100%}}@keyframes appear{0%{background-color:#121212;background-size:0 0;-webkit-filter:blur(5px);-moz-filter:blur(5px);-o-filter:blur(5px);-ms-filter:blur(5px);filter:blur(5px)}10%{background-color:#121212;background-size:10% 10%}30%{background-color:#121212;background-size:40% 10%}50%{background-color:#121212;background-size:80% 10%}80%{background-size:100% 50%;-webkit-filter:blur(0);-moz-filter:blur(0);-o-filter:blur(0);-ms-filter:blur(0);filter:blur(0)}100%{background-size:100% 100%}}

.projects-site{display:flex;justify-content:center;align-items:center;flex-wrap:wrap;width:100%;box-shadow:0px 3px 7px -2px rgba(0,0,0,0.52)}.projects-site h2{width:100vw;padding:10vh 0;font-weight:300;opacity:0.9;text-align:center;font-size:6vh;color:#fff;background-color:#fff;background:linear-gradient(-45deg, #ee7752, #e73c7e, #23a6d5, #23d5ab);background-size:400% 400%;box-shadow:0px 3px 7px -2px rgba(0,0,0,0.52);margin-top:10vh;-webkit-animation:gradient 15s ease infinite;animation:gradient 15s ease infinite}.projects-site a{width:20vw;height:4vh;display:flex;justify-content:center;align-items:center;background:linear-gradient(-45deg, #dfdfdf, #dfdfdf, #959595, #dfdfdf);background-size:400% 400%;box-shadow:0px 3px 7px -2px rgba(0,0,0,0.52)}.projects-site a:nth-of-type(odd){margin:0 10px 0 15vw}.projects-site a:nth-of-type(even){margin:0 15vw 0 10px}.projects-site a:hover{-webkit-animation:gradient 2s 0.15s linear;animation:gradient 2s 0.15s linear}.projects-site img{height:90%}@-webkit-keyframes gradient{0%{background-position:0% 50%}50%{background-position:100% 50%}100%{background-position:0% 50%}}@keyframes gradient{0%{background-position:0% 50%}50%{background-position:100% 50%}100%{background-position:0% 50%}}

.form{min-width:350px;width:50%;height:80vh;display:flex;flex-direction:column;flex-wrap:wrap;padding:2vh 5vw}.form h1{padding:1vh 0;margin-bottom:2vh;letter-spacing:2px;box-shadow:0 4px 2px -2px rgba(0,0,0,0.52)}.form input,.form textarea{background-color:transparent;border:none;font-size:1.2rem;padding:10px 10px;margin-top:10px;color:#000;letter-spacing:1px;box-shadow:0 2px 2px -2px rgba(0,0,0,0.52)}.form textarea{height:200px}.form .button{display:block;margin:0 0 auto auto;width:120px;justify-self:end;background-color:#008cba;color:#fdfdfd;letter-spacing:2px;margin-top:3vh}

.contact-site{width:100%;min-height:80%;display:flex;flex-wrap:wrap;justify-content:center;letter-spacing:1px}.contact-site a:hover{text-decoration:underline}.contact__section{min-width:350px;width:50%;height:80vh;min-height:50vh;display:flex;flex-direction:column;padding:2vh 6vw;opacity:0.9}.contact__title{padding:1vh 0;margin-bottom:2vh;box-shadow:0 4px 2px -2px rgba(0,0,0,0.52)}.contact__subtitle{padding-bottom:2vh}.contact__text{font-size:1.2rem;padding-bottom:2vh}

.howTo{width:99%;height:80%;display:flex;justify-content:start;align-items:flex-start;flex-direction:column;flex-wrap:wrap;box-shadow:0 0 5px #333;letter-spacing:2px}.howTo form{width:40%;height:10%;min-width:200px;display:flex;justify-content:start;align-items:center;background-color:#008cba;border-radius:2px solid #008cba;z-index:1;color:white;font-weight:700;box-shadow:3px 4px 2px -2px rgba(51,51,51,0.431)}.howTo form label{padding-left:20px;margin-right:10px;font-size:1.2rem}.howTo form input{width:50%;min-width:1%;font-size:1rem;padding:5px 10px;background-color:transparent;border:none;border-bottom:1px solid;color:white}.howTo form input:focus{border:1px solid #333}.howTo__list{flex-grow:1;width:40%;min-width:200px;height:80%;overflow-y:scroll;overflow-x:hidden}.howTo__list .active{background:#a1c4fd8f}.howTo__list .active:hover{background:#a1c4fd8f}.howTo__list li{width:100%;padding:5px 5px 5px 20px;display:flex;align-self:center;cursor:pointer;font-size:1rem;line-height:1.2rem}.howTo__list li:nth-of-type(even){background-color:rgba(195,195,195,0.486)}.howTo__list li:nth-of-type(even).active{background:#a1c4fd8f}.howTo__list li:hover{background-color:rgba(128,196,251,0.664)}.howTo__description{position:relative;height:100%;width:60%;display:flex;justify-content:center;align-items:center}.howTo__description-top,.howTo__description-bottom{position:absolute;background-color:#fdfdfd;width:100%;display:flex;padding:0 20px}.howTo__description-top{top:0;left:0;height:10%;justify-content:end;align-items:center;background-color:#008cba;box-shadow:0 4px 2px -2px rgba(51,51,51,0.431);z-index:1}.howTo__description-top button{font-size:1rem;box-shadow:none;transition:0.15s;background-color:transparent;padding:0;color:white}.howTo__description-top button:hover{text-decoration:underline;transform:none}.howTo__description-mid{display:flex;align-items:flex-start;padding:50px 10%;margin-bottom:50px;width:100%;height:80%;text-align:justify;font-size:1rem;overflow-y:scroll;overflow-x:hidden;line-height:1.2rem;background-color:#fdfdfd}.howTo__description-bottom{align-items:center;bottom:0;left:0;height:10%;box-shadow:0 -3px 2px -2px rgba(51,51,51,0.253)}.howTo__description-bottom .new{display:flex;justify-content:center;align-items:center;height:80%;background-color:#4caf50;color:white;font-weight:700;padding:0px 8px;border-radius:0;box-shadow:0 0 5px #333}.howTo__description a{color:rgba(90,2,161,0.76);transition:0.15s;margin:0 auto 0 10px}.howTo__description a:hover{text-decoration:underline}.howTo__modal{position:fixed;width:90%;height:60%;top:0;right:0;bottom:0;left:0;margin:auto;display:flex;align-items:flex-start;background-color:#fff;border:1px solid #000;box-shadow:0 0 5px #333;z-index:99}.howTo__modal form{height:90%;width:90%;margin:auto;display:flex;flex-wrap:wrap;align-items:flex-start;flex-direction:row}.howTo__modal form label{display:block;display:flex;flex-grow:0;width:20%}.howTo__modal form input,.howTo__modal form textarea{width:70%;padding:0 20px;height:-webkit-fit-content;height:-moz-fit-content;height:fit-content}.howTo__modal form textarea{height:40%}.howTo__modal .exit{position:absolute;top:10px;right:20px;font-size:4vh;margin:auto;cursor:pointer;transform:rotate(45deg);-webkit-user-select:none;-moz-user-select:none;-ms-user-select:none;user-select:none}


